
body{background:white;font-family:"Raleway",Helvetica,Arial,sans-serif
}
h1,h2,input,select,textarea{font-family:"Raleway",Helvetica,Arial,sans-serif
}
h1{font-weight:900;color:black;position:relative
}
h1.title-with-icon::before{content:"";position:absolute;bottom:3px;left:83px;width:30px;height:8px;background:#ddd
}
