.main-layout-transparent .mdl-layout__header{background:linear-gradient(20deg, #4a148c, #b71c1c) no-repeat center center;background-size:cover;display:block
}
.page-content{padding:20px;max-width:720px;margin:0 auto
}
.mdl-layout-title{font-weight:bold
}
.mdl-layout-title a{color:white;text-decoration:none
}
.mdl-layout-title a:hover{color:#ccc
}
.mdl-button.mdl-button--colored{box-shadow:rgba(0,0,0,0.2) 0 -4px 0 inset;border-radius:2px
}
.rounded-icon{border-radius:50%;background:black;color:white;padding:10px
}
.icon-48{font-size:48px;vertical-align:middle
}
.mdl-grid.mdl-grid-negative{margin:0 -16px
}
@media (max-width: 479px){
.mdl-responsive-table-box{overflow-x:auto
}
.mdl-responsive-table-box .mdl-data-table{width:100%
}
.mdl-responsive-table-box .mdl-data-table td{white-space:nowrap
}
}
.mdl-layout__drawer-button{color:white
}
@media (min-width: 480px){
.mdl-layout__drawer-button{display:none
}
}
.mdl-layout__header-row{padding:0 40px 0 60px
}
@media (min-width: 480px){
.mdl-layout__header-row{padding:0 40px
}
}
.demo-card-wide.mdl-card{margin:0 0 20px 0;width:100%
}
.demo-card-wide>.mdl-card__title{color:#fff;height:176px;background:url(https://getmdl.io/assets/demos/welcome_card.jpg) center/cover
}
.demo-card-wide>.mdl-card__menu{color:#fff
}
.demo-card-wide .mdl-button--colored{box-shadow:none
}
.fade-enter-active,.fade-leave-active{transition:opacity 200ms, transform 200ms;transform:translateY(0px)
}
.fade-enter,.fade-leave-active{opacity:0;transform:translateX(-10px)
}
.mdl-snackbar.mdl-snackbar-large{background:linear-gradient(20deg, #455a64, #78909c) no-repeat center center;padding:15px 20px
}
@media (min-width: 480px){
.mdl-snackbar.mdl-snackbar-large{padding:20px 40px
}
}
.mdl-snackbar.mdl-snackbar-large .mdl-snackbar__text{font-size:18px;line-height:1.4;color:white;font-weight:100;font-family:"Raleway",Helvetica,Arial,sans-serif
}
@media (min-width: 480px){
.mdl-snackbar.mdl-snackbar-large .mdl-snackbar__text{font-size:22px
}
}
.mdl-textfield-full{width:100%
}
.welcome-user{display:inline-block;margin-left:20px
}



